import './css/style.css';

const state = {
  tools: {
    pencil: true,
    bucket: false,
    picker: false,
  },
  colors: {
    current: '#c4c4c4',
    prev: '#41f795',
  },
  canvas: {
    width: 0,
    height: 0,
  },
  block: {
    width: 0,
    height: 0,
  },
};

const getSizes = (canvasItem) => {
  const st = state;
  st.canvas.width = canvasItem.width;
  st.canvas.height = canvasItem.height;
  st.block.width = canvasItem.width / 128;
  st.block.height = canvasItem.height / 128;
};

const lineBr = (ctx, x1, y1, x2, y2, pxW, pxH, clr) => {
  let newX1 = Math.round(x1 / pxW);
  let newY1 = Math.round(y1 / pxH);
  const newX2 = Math.round(x2 / pxW);
  const newY2 = Math.round(y2 / pxH);
  const dx = Math.abs((newX2 - newX1));
  const sx = newX1 < newX2 ? 1 : -1;
  const dy = -Math.abs(newY2 - newY1);
  const sy = newY1 < newY2 ? 1 : -1;
  let e2;
  let er = dx + dy;
  let end = false;
  ctx.beginPath();
  ctx.fillStyle = clr;
  while (!end) {
    ctx.rect(newX1 * pxW, newY1 * pxH, pxW, pxH);
    if (newX1 === newX2 && newY1 === newY2) {
      end = true;
    } else {
      e2 = 2 * er;
      if (e2 > dy) {
        er += dy;
        newX1 += sx;
      }
      if (e2 < dx) {
        er += dx;
        newY1 += sy;
      }
    }
  }
  ctx.fill();
};

const dataStorage = (canvasItem, ctxItem) => {
  const { width, height } = state.canvas;
  if (localStorage.getItem('image')) {
    const img = new Image();
    img.onload = () => {
      ctxItem.clearRect(0, 0, width, height);
      ctxItem.drawImage(img, 0, 0);
    };
    img.src = localStorage.getItem('image');
    ctxItem.drawImage(img, 0, 0);
  }

  const dataSave = () => {
    localStorage.setItem('image', canvasItem.toDataURL());
  };

  window.addEventListener('unload', dataSave);
};

const pencilDrawing = (canvasItem, ctxItem) => {
  const pencilDraw = (e) => {
    const { pencil } = state.tools;
    const { width, height } = state.block;
    const { current: color } = state.colors;

    if (e.buttons > 0 && pencil === true) {
      const x = e.offsetX;
      const y = e.offsetY;
      const dx = e.movementX;
      const dy = e.movementY;
      lineBr(ctxItem, x - dx, y - dy, x, y, width, height, color);
    }
  };

  canvasItem.addEventListener('mousemove', pencilDraw);
  canvasItem.addEventListener('mousedown', pencilDraw);
};

const penSizeChanger = () => {
  const rangeInput = document.querySelector('.pixel-range > input[type="range"]');
  const rangeLabel = document.querySelector('.pixel-range > label');

  const pixelSizeChange = (event) => {
    const st = state;
    st.block.width = st.canvas.width / event.target.value;
    st.block.height = st.canvas.height / event.target.value;
  };
  const rangeInputListener = (e) => {
    pixelSizeChange(e);
    rangeLabel.innerText = e.target.value;
  };

  rangeInput.addEventListener('change', rangeInputListener);
};

const colorPicker = (canvasItem, ctxItem) => {
  const colorContainer = document.querySelector('.colors');
  const colorInput = document.querySelector('input[type="color"]');

  const changeColor = (predefinedColor = null) => {
    const colorState = state.colors;
    const currColor = document.querySelector('.curr > span');
    const prevColor = document.querySelector('.prev > span');
    const tempColor = colorState.current;
    if (predefinedColor) {
      colorState.current = predefinedColor;
      colorState.prev = tempColor;
    } else {
      colorState.current = colorState.prev;
      colorState.prev = tempColor;
    }
    currColor.style.background = colorState.current;
    prevColor.style.background = colorState.prev;
  };

  const colorChangeFromColorBar = (e) => {
    if (e.target.classList.contains('curr') || e.target.classList.contains('prev')) {
      changeColor();
    }

    if (e.target.classList.contains('red') || e.target.classList.contains('blue')) {
      changeColor(e.target.getAttribute('data-color'));
    }
  };

  const colorPickFromCanvas = (e) => {
    const { picker } = state.tools;
    const x = e.offsetX;
    const y = e.offsetY;

    const rgbToHex = (r, g, b) => [r, g, b].map((ex) => {
      const hex = ex.toString(16);
      return hex.length === 1 ? `0${hex}` : hex;
    }).join('');

    if (picker === true) {
      const rgba = ctxItem.getImageData(x, y, 1, 1).data;
      changeColor(`#${rgbToHex(...rgba)}`);
    }
  };

  const colorInputListener = (e) => {
    changeColor(e.target.value);
  };

  colorInput.addEventListener('change', colorInputListener);
  colorContainer.addEventListener('click', colorChangeFromColorBar);
  canvasItem.addEventListener('click', colorPickFromCanvas);
};

const toolBar = () => {
  const buttons = document.querySelector('.tools');

  const toolChoose = (e) => {
    const toolbar = state.tools;
    [].forEach.call(buttons.children, (button) => {
      button.classList.remove('active');
    });

    if (e.target.getAttribute('data-tool') === 'pencil' || e.code === 'KeyP') {
      toolbar.pencil = true;
      toolbar.bucket = false;
      toolbar.picker = false;
      if (e.code) {
        document.querySelector('li[data-tool="pencil"]').classList.add('active');
      } else {
        e.target.classList.add('active');
      }
    }

    if (e.target.getAttribute('data-tool') === 'bucket' || e.code === 'KeyB') {
      toolbar.pencil = false;
      toolbar.bucket = true;
      toolbar.picker = false;
      if (e.code) {
        document.querySelector('li[data-tool="bucket"]').classList.add('active');
      } else {
        e.target.classList.add('active');
      }
    }

    if (e.target.getAttribute('data-tool') === 'picker' || e.code === 'KeyC') {
      toolbar.pencil = false;
      toolbar.bucket = false;
      toolbar.picker = true;
      if (e.code) {
        document.querySelector('li[data-tool="picker"]').classList.add('active');
      } else {
        e.target.classList.add('active');
      }
    }
  };

  window.addEventListener('keypress', toolChoose);
  buttons.addEventListener('click', toolChoose);
};

const colorFill = (ctxItem, canvasItem) => {
  const fillRect = () => {
    const { bucket } = state.tools;
    if (bucket === true) {
      const context = ctxItem;
      const { width, height } = state.canvas;
      const { current: currColor } = state.colors;
      context.fillStyle = currColor;
      context.fillRect(0, 0, width, height);
    }
  };

  canvasItem.addEventListener('click', fillRect);
};

const imageCanvas = (ctxItem) => {
  const searchInput = document.querySelector('.get-city > input[type="text"]');
  const loadButton = document.querySelector('.get-city > input[type="submit"]');

  const imgPosition = (imgInst, context) => {
    const image = imgInst;
    let zoom = 0;
    let dx = 0;
    let dy = 0;
    const canvasWidth = state.canvas.width;
    const canvasHeight = state.canvas.height;
    let imgWidth = image.width;
    let imgHeight = image.height;
    if (imgWidth > imgHeight && imgWidth < canvasWidth) {
      zoom = canvasWidth / imgWidth;
      imgWidth *= zoom;
      imgHeight *= zoom;
      dx = 0;
      dy = (canvasHeight - (imgHeight)) / 2;
    } else if (imgHeight > imgWidth && imgHeight < canvasHeight) {
      zoom = canvasHeight / imgHeight;
      imgWidth *= zoom;
      imgHeight *= zoom;
      dx = (canvasWidth - (imgWidth)) / 2;
      dy = 0;
    }

    if (zoom !== 0) {
      context.clearRect(0, 0, canvasWidth, canvasHeight);
      context.drawImage(image, dx, dy, imgWidth, imgHeight);
    }
  };

  const getCity = async (town) => {
    const url = 'https://api.unsplash.com/photos/random';
    const apiKey = '2b23c5dcc6061d4bb277c7f9541cf9e76ad00e890bdceb6f23505936ae822446';
    const queryTown = `town,${town}`;
    const townUrl = `${url}?query=${queryTown}&client_id=${apiKey}`;
    const respData = await fetch(townUrl);
    const transformData = await respData.json();
    const img = new Image();
    img.src = transformData.urls.small;
    const delim = 1 * transformData.width / 400;
    img.width = transformData.width / delim;
    img.height = transformData.height / delim;
    img.setAttribute('crossOrigin', '');
    img.addEventListener('load', () => {
      imgPosition(img, ctxItem);
    });
  };

  const loadButtonListener = (e) => {
    e.preventDefault();
    if (searchInput.value) {
      getCity(searchInput.value);
    }
  };

  loadButton.addEventListener('click', loadButtonListener);
};

const bwTransform = (ctxItem) => {
  const bwButton = document.querySelector('.bw-button');

  const bwTransformationProcess = () => {
    const { width, height } = state.canvas;
    const imageData = ctxItem.getImageData(0, 0, width, height);
    const { data } = imageData;
    const arraylength = width * height * 4;

    for (let i = arraylength - 1; i > 0; i -= 4) {
      const gray = 0.3 * data[i - 3] + 0.59 * data[i - 2] + 0.11 * data[i - 1];
      data[i - 3] = gray;
      data[i - 2] = gray;
      data[i - 1] = gray;
    }
    ctxItem.putImageData(imageData, 0, 0);
  };

  const bwButtonListener = () => {
    bwTransformationProcess();
  };

  bwButton.addEventListener('click', bwButtonListener);
};

const init = () => {
  const canvas = document.getElementById('palette');
  const ctx = canvas.getContext('2d');
  getSizes(canvas);
  dataStorage(canvas, ctx);
  pencilDrawing(canvas, ctx);
  colorPicker(canvas, ctx);
  toolBar();
  colorFill(ctx, canvas);
  penSizeChanger();
  imageCanvas(ctx);
  bwTransform(ctx);
};

init();
